.container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: auto;
    min-height: 500px;
}

.h2 {
    font-family: 'PT Sans', serif;
    font-size: 2em;
    margin: 16px 0 0;
    color: darkgreen;
}

.h3 {
    margin: 24px 0 0;
    font-size: 2em;
    font-family: "Share Tech Mono", serif;
}

.data-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.img {
    width: 100px;
    height: 100px;
}